// animation: -name -duration -timing-function -delay -iteration-count -direction;
@keyframes colorChange {
  0% {
    background: radial-gradient(
      circle,
      rgba(246, 173, 173, 1) 0%,
      rgba(190, 109, 109, 1) 25%,
      rgba(166, 57, 57, 1) 50%,
      rgba(119, 27, 27, 1) 100%
    );
  }

  25% {
    background: radial-gradient(
      circle,
      rgba(250, 165, 123, 1) 0%,
      rgba(190, 132, 109, 1) 25%,
      rgba(166, 90, 57, 1) 50%,
      rgba(77, 40, 2, 1) 100%
    );
  }

  50% {
    background: radial-gradient(
      circle,
      rgba(250, 208, 123, 1) 0%,
      rgba(190, 169, 109, 1) 25%,
      rgba(166, 134, 57, 1) 50%,
      rgba(136, 122, 21, 1) 100%
    );
  }

  100% {
    background: radial-gradient(
      circle,
      rgba(242, 240, 57, 1) 0%,
      rgba(200, 195, 88, 1) 25%,
      rgba(208, 186, 35, 1) 50%,
      rgba(171, 164, 17, 1) 100%
    );
  }
}

@keyframes colorChangeAnimistaLight {
  0% {
    background: #19dcea;
  }

  25% {
    background: #b22cff;
  }

  50% {
    background: #ea2222;
  }

  75% {
    background: #f5be10;
  }

  100% {
    background: #3bd80d;
  }
}

@keyframes colorChangeAnimistaDark {
  0% {
    background: #107f87;
  }

  25% {
    background: #7520a7;
  }

  50% {
    background: #ea2222;
  }

  75% {
    background: #5e4905;
  }

  100% {
    background: #207606;
  }
}

@keyframes slit-in-vertical {
  0% {
    -webkit-transform: translateZ(-800px) rotateY(90deg);
    transform: translateZ(-800px) rotateY(90deg);
    opacity: 0;
  }

  54% {
    -webkit-transform: translateZ(-160px) rotateY(87deg);
    transform: translateZ(-160px) rotateY(87deg);
    opacity: 1;
  }

  100% {
    -webkit-transform: translateZ(0) rotateY(0);
    transform: translateZ(0) rotateY(0);
  }
}

@keyframes flicker-1 {
  0%,
  100% {
    opacity: 1;
  }

  41.99% {
    opacity: 1;
  }

  42% {
    opacity: 0;
  }

  43% {
    opacity: 0;
  }

  43.01% {
    opacity: 1;
  }

  47.99% {
    opacity: 1;
  }

  48% {
    opacity: 0;
  }

  49% {
    opacity: 0;
  }

  49.01% {
    opacity: 1;
  }
}

@keyframes jello-horizontal {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  30% {
    -webkit-transform: scale3d(1.25, 0.75, 1);
    transform: scale3d(1.25, 0.75, 1);
  }

  40% {
    -webkit-transform: scale3d(0.75, 1.25, 1);
    transform: scale3d(0.75, 1.25, 1);
  }

  50% {
    -webkit-transform: scale3d(1.15, 0.85, 1);
    transform: scale3d(1.15, 0.85, 1);
  }

  65% {
    -webkit-transform: scale3d(0.95, 1.05, 1);
    transform: scale3d(0.95, 1.05, 1);
  }

  75% {
    -webkit-transform: scale3d(1.05, 0.95, 1);
    transform: scale3d(1.05, 0.95, 1);
  }

  100% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

@keyframes bounce-top {
  0% {
    -webkit-transform: translateY(-45px);
    transform: translateY(-45px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
    opacity: 1;
  }

  24% {
    opacity: 1;
  }

  40% {
    -webkit-transform: translateY(-24px);
    transform: translateY(-24px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  65% {
    -webkit-transform: translateY(-12px);
    transform: translateY(-12px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  82% {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  93% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  25%,
  55%,
  75%,
  87% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }

  100% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
    opacity: 1;
  }
}

@keyframes swing-in-bottom-fwd {
  0% {
    transform: translateY(100%);
    transform-origin: bottom;
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    transform-origin: bottom;
    opacity: 1;
  }
}
// hello btn
@keyframes ticker {
  0%,
  100% {
    transform: rotate(0);
  }
  10%,
  30%,
  50%,
  70% {
    transform: rotate(-30deg);
  }
  20%,
  40%,
  60% {
    transform: rotate(30deg);
  }
  80% {
    transform: rotate(10deg);
  }
  90% {
    transform: rotate(-10deg);
  }
}

@keyframes wakeup {
  0% {
    transform: rotate(180deg) scale(0);
  }
  100% {
    transform: rotate(180deg) scale(1);
  }
}
